import React from "react"
import { NavLink } from "react-router-dom";
import { useState, useEffect, useContext } from "react"
import firebase from "firebase";
import DropDown from "./sub-com/dropdownProfile"
import { useHistory } from "react-router-dom"
import Moosix from "./functions/mossix-api"
import io from "./functions/notification.js"
import {uidContext} from "../context/uid"
import {songContext} from "../context/songContext"
import Notification from "./notification/notification"
const app = new Moosix()
function Header(props){


    const [isHidden3, setIsHidden3] = useState(true)
        const [isHidden2, setIsHidden2] = useState(true)
        const [searchTerm, setSearchTerm] = useState(null)
        const [pfp, setPfp] = useState(null)
        const [userID, setUserID] = useState(null);
        const history = useHistory();
        const [newNotification, setNewNotification] = useState(false)
        const [isHidden, setIsHidden] = useState(true)
        const [list, setList] = useState()
        const uid = useContext(uidContext)
        const song = useContext(songContext)
        const socket =  io(uid)
        
     
        useEffect(()=>{
            if(socket){
                socket.on("notification", data=>{
                    if(data){
                        setNewNotification(data.newNotification)
                    }
                })
            }
        }, [uid, socket])

        firebase.auth().onAuthStateChanged((user)=>{
            if(user){
               setUserID(user.uid)
            }else{
                history.push("/login")
            } 
        })
        window.onclick = (event) =>{
            if(event.target.id  !== "searchBar" ){
                setIsHidden(true)
            }
            if(event.target.id == "profile-drop" && isHidden2){
                setIsHidden2(false)//false means drop down is active
            } 
            else if(event.target.id !== "profile-drop"){
                setIsHidden2(true)//drop down is not active
            } 
            else if(event.target.id == "profile-drop" && isHidden2 == false){
                setIsHidden2(true)
            }
           
            if(event.target.id == "notification-button" && isHidden3){
                setNewNotification(false)
                setIsHidden3(false)
            }
            else if(event.target.id == "notification-button" &&  isHidden3 == false){
                setIsHidden3(true)
            }
            else if(event.target.className == "notification-box"){
               setIsHidden(false)
            }
            else{
                setIsHidden3(true)
            }
            
        }
               
            

  
        
        useEffect(()=>{
            if(searchTerm != ""){
                setIsHidden(false)
                app.searchQ(searchTerm).then((list)=> setList(list))
            }else{
                setIsHidden(true)
            }
        }, [searchTerm])
            
        function autoSearch(event){
            Search(event.target.getAttribute('data-name'))
        }
        function Search(s){
            setIsHidden(true)
            if(s != ""){
                history.push("/results?search="+s)
            }
        }
       

        return(
            <header>
                <div id = "hd-container">
                    <div id = "logo-contain">
                        <NavLink to = "/"><h1></h1></NavLink>
                    </div>
                    <div id ="search">
                        <div className = "bar">
                            <input 
                                id = "searchBar" 
                                placeholder = "search" 
                                autoComplete = "off"
                                onKeyUp = {event=>event.key == "Enter"?Search(searchTerm):null }
                                onChange = {event=>{
                                    setSearchTerm(event.target.value.toLocaleLowerCase())    
                                }}
                            />
                        </div>
                        <div className = "searchBtn">
                            <span id = "srhBtn" className = "material-icons" onClick = {()=>Search(searchTerm)}>search</span>    
                        </div>
                        {isHidden?null:
                        <div className = "searchList-wrapper">
                            <div className = "searchList-holder">
                                <ul>
                                    {
                                        list && list.map((val, key)=>{  
                                        return(
                                            <li className = "lists" key = {key} >
                                                <div className = "search-list" >
                                                    <span onClick = {autoSearch} data-name = {val.info.title} id = "autolist"> {val.info.title}</span>
                                                </div>  
                                            </li>
                                        )
                                    })}
                                </ul>
                            </div>
                            
                        </div>
                        }                   
                    </div>

                   
                    <>
                        <div id = "buttons">
                            <div className = "profile">
                                    <img src = {pfp} height = "25" width = "25" alt = "" id = "profile-drop"/>
                            </div>
                            <div className = "notification-button" >
                                {newNotification? <span id = "notification-button" className="material-icons">notifications_active</span>: <span id = "notification-button" className="material-icons">notifications</span>}  
                            </div>
                        </div>
                        {isHidden3?null:  <Notification />}
                      
                        
                       {isHidden2? null:  <DropDown uid = {userID}/>}
                    </>
                    
                
                </div>
            </header>
        )
}
export default Header;

